.give_youself_goosebumps_reader_beware_you_choose_the_scare_container {
  display: flex;
  flex-direction: column;

  .header {
    display: flex;
    justify-content: space-between;
    max-width: 550px;

    &-image {
      width: 250px;
      height: 190px;
      top: 0;
      left: 0;
    }

    &-link {
      position: absolute;
      display: flex;
      flex-direction: column;
      height: 45px;
      justify-content: space-between;
      top: 20px;
      left: 160px;

      a {
        text-decoration: none;
        font-size: 14px;
        color: rgb(202, 165, 202);
      }
    }

    .item {
      width: 250px;

      img {
        width: 100%;
        height: 96.61px;
      }

      .image2 {
        width: 230px;
        height: 100px;
      }

      .header-image {
        height: auto;
      }
    }
  }
  .give_youself_goosebumps_reader_beware_you_choose_the_scare-main {
    margin: -80px 300px;
    display: grid;
    grid-template-columns: repeat(4, 214px);
    grid-column-gap: 45px;
    grid-row-gap: 15px;

    .card-item {
      width: 214px;
      height: 200px;
      border: 2px solid black;
      padding: 5px;
      box-sizing: border-box;

      .card-image1 {
        width: 100px;
        height: 148px;
        margin-left: 100px;
      }

      .card-link {
        text-decoration: none;
        color: black;

        .card-image-block {
          display: flex;

          .image-of-block {
            width: 100px;
            height: 148px;
          }
        }
      }
    }
  }

  .special-edition-image {
    width: 150px;
    height: 25px;
    margin: 100px 0 0 302px;
  }

  .special_edition_series {
    margin: 20px 0 40px 300px;
    display: grid;
    grid-template-columns: repeat(4, 214px);
    grid-column-gap: 45px;
    grid-row-gap: 15px;

    .card-item {
      width: 214px;
      height: 200px;
      border: 2px solid black;
      padding: 5px;
      box-sizing: border-box;

      .card-image1 {
        width: 100px;
        height: 148px;
        margin-left: 100px;
      }

      .card-link {
        text-decoration: none;
        color: black;

        .card-image-block {
          display: flex;

          .image-of-block {
            width: 100px;
            height: 148px;
          }
        }
      }
    }
  }
}
